* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  font-family: 'Roboto Condensed', sans-serif;
  text-align: center;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(#e38ac8, #7ce397);
}

header {
  text-align: center;
  width: 80vh;
  margin: 2rem auto;
  padding: 2rem;
}

.inputForm {
  width: 80vh;
  margin: 0 auto;
  text-align: center;
  position: relative;
}

.inputForm input {
  width: 15rem;
  height: 1.7rem;
  padding: 0.5rem;
  border: none;
}

.inputForm button {
  padding: 0.38rem 0.5rem;
  background: rgb(243, 240, 84);
}

.todoList {
  width: 100vw;
  margin: 2rem auto;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

.todo {
  margin: 0.4rem;
  display: flex;
  text-align: center;
  justify-content: center;
  position: relative;
}

.todoText {
  min-width: 15rem;
  max-height: 1.8rem;
  padding: 0.3rem 0.4rem;
  background: white;
}

.todo .fa-check {
  padding: 0.487rem .5rem;
  background-color: rgb(83, 164, 195);
}

.todo .fa-trash {
  padding: 0.487rem .5rem;
  background-color: rgb(197, 70, 81);
}

button {
  border: none;
  cursor: pointer;
}

button:hover {
  opacity: 0.7;
}